import React from 'react';
import { render } from 'react-dom';
import Events from './patterns/events/events';
import { finderConfig } from './patterns/finder/funnelback';

document.addEventListener('DOMContentLoaded', () => {
    if (/(Trident|MSIE)/.test(navigator.userAgent)) {
        return;
    } else {
        const events = document.querySelector('.wrapper--events');
        events &&
            finderConfig(events.dataset.config).then((config) => {
                render(<Events config={config} element={events} />, events);
            });
    }
});
